<template>
  <v-app>
    <v-content >
      <v-container>
         <h></h>
         <v-container text-xs-center>
       <v-flex xs12 sm6 offset-sm3>
        <v-card dark color="primary">
          <v-card-text class="px-0">MY BOOKS</v-card-text>
        </v-card>
      </v-flex>
      </v-container>
        <template>
  <v-card>
    <v-card-title>
      MY BOOKS
      <v-spacer></v-spacer>
      <v-text-field
        v-model="search"
        append-icon="search"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="mybooks"
      :search="search"
    >
      <template v-slot:items="props">
        <td>{{ props.item.Book.title }}</td>
       
        <td class="text-xs-right">{{ props.item.Book.author }}</td>
        <td class="text-xs-right">{{ props.item.issued_Date }}</td>
        <td class="text-xs-right">{{ props.item.fine}}</td>
        <td class="text-xs-right">
          <v-btn small color="green lighten-2" :disabled='props.item.isReturned' @click="dialog=true,id=props.item.book_Id">return</v-btn>
         
          </td>
      </template>
      <v-alert v-slot:no-results :value="true" color="error" icon="warning">
        Your search for "{{ search }}" found no results.
      </v-alert>
      <template v-slot:footer>
      <td :colspan="headers.length">
        <strong>TOTAL BOOKS BORROWED:</strong>
      </td>
    </template>
    
    </v-data-table>
  </v-card>
</template>
<v-dialog v-model="dialog" width="500" lazy>
      <v-card>
     <v-card-text>
         do u want submit return request?
    </v-card-text>
     <v-divider></v-divider>
     <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" flat @click="request()"> yes</v-btn>
          <v-btn color="primary" flat @click="dialog=false">cancel</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="suc" width="500" lazy>
      <v-card>
     <v-card-text>
         book return request sent successfully
    </v-card-text>
     <v-divider></v-divider>
     <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" flat @click="refresh()">OK</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <v-dialog v-model="fail" width="500" lazy>
      <v-card>
     <v-card-text>
        {{message}}
    </v-card-text>
     <v-divider></v-divider>
     <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" flat @click="fail=false">OK</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
      </v-container>
    </v-content>
  </v-app>
</template>

<script>
import h from './hstudent.vue';

export default {
  name: 'App',
  components: {
    h
  },
  methods:{
      request(){
        this.dialog=false;
        let t={
          user_Id:this.$store.state.userId,
          book_Id:this.id
        }
        console.log("return req obj"+t);
         console.log("return req obj"+JSON.stringify(t));

         this.$http.post("http://localhost:3000/api/transactions/return",t).then(result => {
                this.suc=true;
               
            }, error => {
              this.message=error.body.error.message;
              this.fail=true;
                console.error(error);
            });
        // this.suc=true
        
      },
      refresh(){
        this.suc=false; 
        this.$router.replace("/student/mybooks");
      }
  },
  data () {
      return {
        search: '',
        mybooks:[],
        dialog:false,
        suc:false,
        fail:false,
        message:'',
        id:'',
        headers: [
          {
            text: 'BOOK TITLE',
            align: 'left',
            value: 'title'
          },
          { text: 'AUTHOR', value: 'author' ,align:'right'},
          { text: 'ISSUE DATE', value: 'issue' ,align:'right'},
          { text: 'FINE', value: 'fine',align:'right' },
          { text: 'ACTION',align:'right' ,value:''},
          
        ],
        
      }
    },
      mounted() {
          let url="http://localhost:3000/api/transactions?filter[include]=Book&filter[where][user_Id]="+this.$store.state.userId;
          console.log(url);
            this.$http.get(url).then(result => {
              
                this.mybooks = result.body;
            }, error => {
                console.error(error);
            });
        },
}
</script>